.app {
  text-align: center;
  font-size: 15px;
}

.pageTitle {
  color: white;
  font-size: 64px;
  font-weight: 400;
  line-height: 85px;
  text-shadow: 0px 2px 4px black;
}

.launchesContainer {
  display: flex;
  justify-content: center;
}

$tableRowBorderRadius: 8px;
$tableRowBorder: 1px solid #ffffff;

table.rocketLaunchesTable {
  table-layout: fixed;
  border-spacing: 0px 8px;
  width: 100%;
  max-width: 958px;
  margin: 32px 64px;

  thead {
    tr {
      background-image: linear-gradient(180deg, #9DB7CD, #FFFFFF);
      height: 49px;
      box-shadow: 0px 2px  10px rgba(8,13,35,0.36);

      th {
        color: #043D67;
        font-weight: 400;
        font-size: 17px;
        line-height: 23px;
      }
    }
  }

  tbody {
    tr {
      background: rgba(255,255,255,0.85);
      height: 64px;
      color: #444444;
      line-height: 20px;

      td {
        border: $tableRowBorder;
        border-left: none;
        border-right: none;
      }

      td:first-child {
        border-top-left-radius: $tableRowBorderRadius;
        border-bottom-left-radius: $tableRowBorderRadius;
        border-left: $tableRowBorder;
      }

      th.badge, td.badge {
        width: 9%;
        text-align: center;

        img {
          vertical-align: middle;
          image-rendering: pixelated;
          height: 64px;
        }
      }

      th.rocketName, td.rocketName {
        width: 13%;
        text-align: left;
      }

      th.rocketType, td.rocketType {
        width: 13%;
        text-align: left;
      }

      th.launchDate, td.launchDate {
        width: 14%;
        text-align: left;
      }

      th.details, td.details {
        width: 36%;
        text-align: left;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      th.id, td.id {
        width: 5%;
        text-align: center;
      }

      th.articleLink, td.articleLink {
        width: 10%;
        text-align: center;
        
        a {
          display: flex;
          justify-content: center;
          align-items: center;

          svg {
            height: 1.3em;

            path {
              fill: #13A2E8;
            }
          }
        }
      }

      td:last-child {
        border-top-right-radius: $tableRowBorderRadius;
        border-bottom-right-radius: $tableRowBorderRadius;
        border-right: $tableRowBorder;
      }
    }
  }
}
